/* VideoPlayer.css */
.video-player {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  min-width: 100%; /* Adjust as needed */
  min-height: 100%; /* Adjust as needed */
  margin: auto;
  background: black;
  overflow: hidden;
  border: none !important;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
}

.video-player video {
  width: 100%;
  height: 100%;
  display: block;
  border: none !important;
}

.controls {
  position: absolute;
  bottom: 1%;
  width: 98%;
  border-radius: 5px;
  margin: 0 auto;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  transition: all 0.2s ease-in-out;
  opacity: 0;
  background: rgba(0, 0, 0, 0.527);
  z-index: 2;
}

.controls.show {
  opacity: 1;
}

.controls > button {
  background-color: transparent;
  border-radius: 5px;
  border: none;
  padding: 0px 10px;
  color: white;
  cursor: pointer;
  font-size: 1.3rem;
}

.controls > i {
  font-size: 1.5rem;
  cursor: pointer;
  margin: 0px 10px;
}

.timeline {
  position: relative;
  flex-grow: 1;
  height: 5px;
  margin: 0 10px;
  background: #ccc;
  cursor: pointer;
}

.progress {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background: var(--blue);
}

.time-display {
  min-width: 80px;
  text-align: right;
  font-size: 0.8rem;
}

.volume-container {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.volume-container i {
  font-size: 1.5rem;
  cursor: pointer;
  margin: 0px 10px;
}

.volume-slider-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.volume-slider {
  position: absolute;
  height: 5px;
  width: 60px;
  background: #ccc;
  cursor: pointer;
  transform: translateX(-85%) translateY(-60px) rotate(-90deg);
}

.settings-container {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.settings-container > i {
  font-size: 1.5rem;
  cursor: pointer;
  margin: 0px 10px;
}

.settings-popup {
  position: absolute;
  top: -6rem;
  right: 0;
  width: auto;
  height: auto;
  font-size: 0.8rem;
  background: var(--black);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: space-between;
  padding: 10px;
  transition: all 0.2s ease-in-out;
}

.settings-popup select {
  background: transparent;
  border: 1px solid var(--white);
  color: var(--white);
  float: right;
  vertical-align: top;
  border-radius: 5px;
  padding: 0px;
  margin: 0px 5px;
  cursor: pointer;
}

.settings-popup select option {
  background: var(--black);
  border: none;
  color: var(--white);
  border: none;
  float: right;
  vertical-align: top;
  border-radius: 5px;
  padding: 0px;
  margin: 0px 5px;
  cursor: pointer;
}

.settings-popup div {
  width: 100%;
  margin-top: 10px;
}

.settings-popup div:first-child {
  width: 100%;
  margin-top: 0px;
}

.settings-popup i {
  margin-right: 5px;
}

.settings-popup input {
  float: right;
  margin: 0px 5px;
  cursor: pointer;
  width: 15px;
  height: 15px;
}
/* Add additional styles below */

.sidebar {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 4%;
  height: 90%;
  background: var(--side-bg);
  border-radius: 5px;
  color: var(--white);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  transition: all 0.2s ease-in-out;
  z-index: 2;
}

.sidebar-top-icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 20px;
  width: 90%;
  border-bottom: 1px solid var(--white);
}
.sidebar-top-icons i:first-child {
  background-color: transparent;
  transform: rotate(-180deg) translateX(10%);
  font-size: 2rem;
}
.sidebar-top-icons i {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  cursor: pointer;
  margin: 10px 10px;
  border-radius: 100%;
  background-color: var(--blue);
  padding: 10px;
  width: 50px;
}

.sidebar-users {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;
  margin-top: 20px;
}

.sidebar-users img {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  margin: 10px 0px;
  background-color: var(--blue);
}
